@import "../../style/variables.scss";

.char {
	&__params {
		top: 25px;
		position: sticky;
	}

	&__info {
		padding: 25px;

		box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);

		z-index: 5;
		background-color: #fff;
		&-name {
			font-weight: bold;
			font-size: 22px;
			line-height: 29px;
			text-transform: uppercase;
		}
		.skeleton {
			margin-top: 30px;
		}
	}
	&__basics {
		display: grid;
		grid-template-columns: 150px auto;
		column-gap: 25px;
		img {
			width: 150px;
			height: 150px;
			object-fit: cover;
		}
	}
	&__btns {
		margin-top: 35px;
		a:nth-child(2) {
			margin-top: 10px;
		}
	}
	&__descr {
		margin-top: 15px;
		font-size: 14px;
		line-height: 18px;
	}
	&__comics {
		font-weight: bold;
		font-size: 18px;
		line-height: 24px;
		margin-top: 10px;
		&-list {
			position: relative;
			margin-top: 10px;
		}
		&-item {
			width: 100%;
			padding: 0px 10px;
			line-height: 25px;
			box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
			margin-top: 10px;
		}
	}
	&__select {
		font-weight: bold;
		font-size: 18px;
		line-height: 24px;
		text-align: center;
	}
}
